import CssBaseline from "@material-ui/core/CssBaseline";
import Layout, { getContent, Root } from "@mui-treasury/layout";
import React, { useEffect, useState } from "react";
import styled from "styled-components";
import Footer from "../components/Footer";
import Header from "../components/Header";
import Sidebar from "../components/Sidebar";
import { MachineLearningContext } from "../context/MachineLearningContext";

const scheme = Layout();

scheme.configureHeader((builder) => {
  builder
    .registerConfig("xs", {
      position: "sticky",
    })
    .registerConfig("md", {
      position: "relative", // won't stick to top when scroll down
    });
});

scheme.configureEdgeSidebar((builder) => {
  builder.create("main", { anchor: "left" }).registerTemporaryConfig("xs", {
    width: "auto", // 'auto' is only valid for temporary variant
  });
});

scheme.configureEdgeSidebar((builder) => {
  builder
    .create("main", { anchor: "left" })
    .registerTemporaryConfig("xs", {
      anchor: "left",
      width: "auto", // 'auto' is only valid for temporary variant
    })
    .registerPermanentConfig("md", {
      width: 256, // px, (%, rem, em is compatible)
      collapsible: true,
      collapsedWidth: 64,
    });
});

const Content = getContent(styled);

const DefaultTemplate = ({ children }) => {
  const [MLData, setMLData] = useState(false);
  useEffect(() => {
    const data = require("../data/data.json");
    setMLData(data);
  }, []);

  return (
    <MachineLearningContext.Provider value={MLData}> 
      <div>
        <Root scheme={scheme}>
          {({ state: { sidebar } }) => (
            <>
              <CssBaseline />
              <Sidebar sidebarState={sidebar.main} />
              <Header />
              <Content>{children}</Content>
              <Footer />
            </>
          )}
        </Root>
      </div>
    </MachineLearningContext.Provider>
  )
};

export default DefaultTemplate;
