import { CardActionArea, CardContent, Grid, Typography } from "@mui/material";
import Card from "@mui/material/Card";
import React from "react";
import { BounceLoader } from "react-spinners";

export const AppCard = ({ children }) => (
  <Grid item xs={12} md={6} xl={4}>
    <Card>
      <CardActionArea>
        <CardContent>{children}</CardContent>
      </CardActionArea>
    </Card>
  </Grid>
);

export const TitleCard = ({ title, children }) => (
  <AppCard>
    <Typography variant="h6" color="text.secondary" gutterBottom>
      {title}
    </Typography>
    {children}
  </AppCard>
);

export const Score = ({ children }) => (
  <Typography variant="h2" align="center">
    {children}
  </Typography>
);

export const Item = ({ title, model, attr, suffix = " tweets", multiplier=1, forceDecimalPlaces=0 }) => (
  <TitleCard title={title}>
    <Score>
      {model &&
        (+attr.split(".").reduce(function (object, property) {
          return object[property];
        }, model) * multiplier).toFixed(forceDecimalPlaces) + suffix}
      {!model && <BounceLoader />}
    </Score>
  </TitleCard>
);

export default Item;