import "./App.scss";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Home from "./pages/Home";
import WordCloud from "./pages/WordCloud";
import NotFound from "./pages/NotFound";
import DefaultTemplate from "./templates/DefaultTemplate";
import MultinomialNB from "./pages/MultinomialNB";
import BernoulliNB from "./pages/BernoulliNB";
import ComplementNB from "./pages/ComplementNB";
import GaussianNB from "./pages/GaussianNB";
import SVC from "./pages/SVC";
import LSVC from "./pages/LSVC";
import KNN from "./pages/KNN";
import Lexical from "./pages/Lexical";


const routerBaseName = process.env.PUBLIC_URL;

function App() {
  return (
    <Router basename={routerBaseName}>
      <DefaultTemplate>
        <Switch>
          <Route path="/cloud">
            <WordCloud />
          </Route>
          <Route path="/mnb">
            <MultinomialNB />
          </Route>
          <Route path="/bnb">
            <BernoulliNB />
          </Route>
          <Route path="/cnb">
            <ComplementNB />
          </Route>
          <Route path="/gnb">
            <GaussianNB />
          </Route>
          <Route path="/svc">
            <SVC />
          </Route>
          <Route path="/lsvc">
            <LSVC />
          </Route>
          <Route path="/knn">
            <KNN />
          </Route>
          <Route path="/lexical">
            <Lexical />
          </Route>
          <Route exact path="/">
            <Home />
          </Route>
          <Route path="/">
            <NotFound />
          </Route>
        </Switch>
      </DefaultTemplate>
    </Router>
  );
}

export default App;
