import { Grid } from "@mui/material";
import React, { useContext } from "react";
import Item from "../../components/PageItem";
import { MachineLearningContext } from "../../context/MachineLearningContext";

const LSVC = () => {
  const modelData = useContext(MachineLearningContext);

  return (
    <Grid container spacing={2} mt={2} mx={4} maxWidth="80%">
      <Item
        title="Acur√°cia (total)"
        model={modelData}
        attr="LSVC.total.accuracy"
        multiplier={100}
        suffix="%"
        forceDecimalPlaces={2}
      />
      <Item
        title="Acur√°cia m√©dia - KFold (total)"
        model={modelData}
        attr="LSVC.total.kfoldAvg"
        multiplier={100}
        suffix="%"
        forceDecimalPlaces={2}
      />
      <Item
        title="Precis√£o (total)"
        model={modelData}
        attr="LSVC.total.precision"
        multiplier={100}
        suffix="%"
        forceDecimalPlaces={2}
      />
      <Item
        title="Revoca√ß√£o (total)"
        model={modelData}
        attr="LSVC.total.recall"
        multiplier={100}
        suffix="%"
        forceDecimalPlaces={2}
      />
      <Item
        title="F1-Score (total)"
        model={modelData}
        attr="LSVC.total.f1"
        multiplier={100}
        suffix="%"
        forceDecimalPlaces={2}
      />
    </Grid>
  );
};

export default LSVC;
