import {
  CardActionArea,
  CardContent,
  CardMedia,
  Grid,
  Typography,
} from "@mui/material";
import Card from "@mui/material/Card";
import React from "react";
import Cloud from "../../data/output.png";
const WordCloud = () => {
  const AppCard = ({ children }) => (
    <Grid item xs={12}>
      <Card>
        <CardActionArea>
          <CardContent>{children}</CardContent>
        </CardActionArea>
      </Card>
    </Grid>
  );

  const TitleCard = ({ title, children }) => (
    <AppCard>
      <Typography variant="h6" color="text.secondary" gutterBottom>
        {title}
      </Typography>
      {children}
    </AppCard>
  );

  return (
    <Grid container spacing={2} mt={2} mx={4} maxWidth="80%">
      <TitleCard title="Word Cloud">
        <CardMedia component="img" image={Cloud} />
      </TitleCard>
    </Grid>
  );
};

export default WordCloud;
