import { Grid, Typography } from "@mui/material";
import React, { useContext } from "react";
import GeneralCard from "../../components/GeneralCard";
import Item from "../../components/PageItem";
import { MachineLearningContext } from "../../context/MachineLearningContext";

const Home = () => {
  const modelData = useContext(MachineLearningContext);

  return (
    <>
      <GeneralCard style={{ display: "flex", textAlign: "center" }} mx={4} ml={6} xs={12} maxWidth="79%">
        <Typography>Este dashboard √© um prot√≥tipo constru√≠do em React + MaterialUI que visa apresentar resultados referentes √† pesquisa "AN√ÅLISE DE SENTIMENTOS DO TWITTER SOBRE A ATUAL SITUA√á√ÉO DA POL√çTICA BRASILEIRA".</Typography>
      </GeneralCard>
      <Grid container spacing={2} mt={2} mx={4} maxWidth="80%">
        <Item
          title="Tamanho do dataset"
          model={modelData}
          attr="dataset.datasetLength"
        />
        <Item
          title="Qtd. de tweets neutros"
          model={modelData}
          attr="dataset.noNeutrals"
        />
        <Item
          title="Qtd. de tweets positivos"
          model={modelData}
          attr="dataset.noPositives"
        />
        <Item
          title="Qtd. de tweets negativos"
          model={modelData}
          attr="dataset.noNegatives"
        />
      </Grid>
    </>
  );
};

export default Home;
